/* global location localStorage window setTimeout*/
import React from 'react'
import { Button, Container } from 'reactstrap'
import { Row, Col } from 'react-flexbox-grid'
import pusher from '../constants/pusher'
import * as actions from '../actions/hostGameActions'
import { connect } from 'react-redux'
import { postRequest } from '../actions/gameAction'
import Swal from 'sweetalert2'
import {
  RESET_GAME,
  GAME_LEADERBOARD,
  GET_SONG_DATA,
  SELECTED_SONG_SKIPPED,
  ADD_FEEDBACK,
} from '../constants/gameConstants'
import { addNewRound } from '../actions/hostGameActions'
import LeaderBoardTable from './LeaderBoardTable'
import Timer from './Timer'
import { checkUserIsLogin } from '../actions/loginActions'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { HelpSection } from './Utils/HelpSection'

class GameRemote extends React.Component {
  state = {
    runOnce: true,
    backgroundMusic: false,
    showBackgroundCheckbox: false,
    nextRound: false,
    enableCurrentSongSkip: false,
    enableNextSongSkip: false,
    triviaMode: false,
    enableReload: false,
    enableReload2: false,
    pusherCurrentSongCount: null,
  }

  UNSAFE_componentWillMount() {
    this.props.dispatch(checkUserIsLogin()).then(res => {
      if (!res) this.props.history.push('/')
      else if (res.account.role != 'host') this.props.history.push('/player/' + this.props.match.params.game_code)
    })
    localStorage.removeItem('game_config_updated')
    this.props.dispatch(actions.getRemoteData({ game: { code: this.props.match.params.game_code } }))
    this.getGameleaderboard()
    this.getSongData()
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (!this.props.game.feedbackUpdated && nextProps.game.feedbackUpdated) {
      this.getSongData()
      if (this._updateCurrentSong)
        this.props.dispatch(actions.getRemoteData({ game: { code: this.props.match.params.game_code } }))
    }
    if (this.state.runOnce && nextProps.game.game) {
      this.setState({ runOnce: false, showBackgroundCheckbox: nextProps.game.game.background_music })
      this.loadGameData(nextProps.game)
      this.loadPlayerGuessData(nextProps.game)
      this._autoAdvance = nextProps.game.game.automatic_song_advance
      this._openSession = nextProps.game.game.open_session
      if (
        nextProps.game &&
        nextProps.game.game &&
        nextProps.game.game.game_mode &&
        nextProps.game.game.game_mode == 'Standard trivia'
      )
        this.setState({ triviaMode: true })

      if (nextProps.game.game && nextProps.game.game.state && nextProps.game.game.state == 'Active Song') {
        setTimeout(() => {
          this.setState({ enableCurrentSongSkip: true, enableNextSongSkip: true })
        }, 5000)
        this._showNextSongs = true
      }
      if (nextProps.game.game && nextProps.game.game.state && nextProps.game.game.state == 'Song Loaded') {
        setTimeout(() => {
          this.setState({ enableCurrentSongSkip: true })
        }, 5000)
      }
    }

    if (nextProps.game && nextProps.game.new_code) location.replace('/config/' + nextProps.game.new_code)
  }

  getSongData() {
    this.props.dispatch(
      postRequest('games/skip_song_data', {
        type: GET_SONG_DATA,
        values: { game: { code: this.props.match.params.game_code } },
      })
    )
  }

  _openSession = false
  _autoAdvance = false
  _showNextSongs = false

  resetGame = () => {
    Swal({
      title: 'HOLD UP!',
      text:
        "Resetting gives you a new game code. This is a last resort if you're in the middle of a game.  If needed, make sure players know about the new code.",
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, Reset Game',
      cancelButtonText: 'No, Keep Using This Game Code',
    }).then(result => {
      if (result.value) {
        this.props.dispatch(
          postRequest('games/reset_game', {
            type: RESET_GAME,
            values: { game: { code: this.props.match.params.game_code } },
          })
        )
        Swal({
          type: 'success',
          title: 'Updated...',
          text: 'Game Reset Successfully!',
          showConfirmButton: false,
          timer: 1500,
        })
      }
    })
  }
  reloadGame() {
    Swal({
      type: 'success',
      title: 'Loading...',
      text: 'Game Loaded Successfully!',
      showConfirmButton: false,
      timer: 1500,
    })
    if (this.state.enableReload && this.props.game && this.props.game.songCount !== this.props.game.totalSongCount) {
      this.props.dispatch(
        postRequest('games/pusher_update', {
          values: {
            game: {
              code: this.props.match.params.game_code,
              status: 'reloadGame',
            },
          },
        })
      )
    } else if (this.state.enableReload2 == true) {
      this.nextSongButton()
    } else {
      this.props.dispatch(
        postRequest('games/pusher_update', {
          values: {
            game: {
              code: this.props.match.params.game_code,
              status: 'pageRefresh',
            },
          },
        })
      )
    }
  }

  gameOver = () => {
    Swal({
      title: 'WAIT UP!',
      text:
        'This is the big moment to see who won!  Be sure you are done with all your drawings before you end the game for the night!',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, End Game',
      cancelButtonText: 'No, Draw Tickets Or Play More',
    }).then(result => {
      if (result.value) {
        this.props.dispatch(
          postRequest('games/pusher_update', {
            values: { game: { code: this.props.match.params.game_code, status: 'gameOver' } },
          })
        )
        Swal({
          type: 'success',
          title: 'Updated...',
          text: 'Game Over Updated Successfully!',
          showConfirmButton: false,
          timer: 1500,
        })
      }
    })
  }

  volumeButton(p) {
    this.props.dispatch(
      actions.sendVolumePusherRequest({ game: { code: this.props.match.params.game_code }, volume: p })
    )
  }

  nextSongButton() {
    this.props.dispatch(actions.advanceSongInGame({ game: { code: this.props.match.params.game_code } }))
    this.setState({ enableNextSongSkip: false })
  }

  skipNextSong() {
    this.props.dispatch(actions.skipAndAddSongCurrentGame({ game: { code: this.props.match.params.game_code } }))
  }

  skipSelectedSong(song_id) {
    this.props.dispatch(
      postRequest(
        'games/skip_song_data',
        {
          type: SELECTED_SONG_SKIPPED,
          values: { game: { code: this.props.match.params.game_code }, songId: song_id },
        },
        true
      )
    )
  }

  loadGameData(gameData) {
    if (gameData.game) {
      let channel_name = 'games_' + gameData.game.id
      const channel = pusher.subscribe(channel_name)
      channel.bind('game_event', data => {
        console.log(data.type)
        switch (data.type) {
          case 'active_song':
            this.setState({ pusherCurrentSongCount: data.song_count })
            this.getSongData()
            this._showNextSongs = true
            setTimeout(() => {
              this.setState({ enableCurrentSongSkip: true, enableNextSongSkip: true })
            }, 5000)
            setTimeout(() => {
              this.setState({ enableNextSongSkip: false })
            }, 50000)
            break
          case 'song_loaded':
            this._showNextSongs = false
            setTimeout(() => {
              this.setState({
                enableCurrentSongSkip: true,
                enableReload: true,
                enableReload2: false,
                pusherCurrentSongCount: data.data.song_of_songs_count,
              })
              this.props.dispatch(actions.getRemoteData({ game: { code: this.props.match.params.game_code } }))
            }, 5000)
            break
          case 'new_round_added':
            setTimeout(() => {
              var url = data.data.replace('/games/', '/config/')
              window.location = url
            }, 11500)
            break
          case 'game_updated':
          case 'start_new_game':
            var url = data.data.replace('/games/', '/config/')
            window.location = url
            break
          case 'showing_leaderboard':
            this._updateCurrentSong = false
            this.setState({
              enableCurrentSongSkip: false,
              enableNextSongSkip: false,
              enableReload: false,
              enableReload2: true,
            })
            break
          case 'skip_song':
            this.setState({ enableNextSongSkip: false, enableCurrentSongSkip: false })
            this.getGameleaderboard()
            break
          case 'guess_end':
            this.getGameleaderboard()
            break
          case 'last_10_songs':
            if (data.remaining_song_count == 0) this.noticeAlert('⚙️ No Songs left to skip in the playlist.')
            else this.noticeAlert('⚙️ Only ' + `${data.remaining_song_count}` + ' songs left in the playlist to skip!')
        }
      })
    }
  }
  noticeAlert(text) {
    toast.success(text, {
      position: 'top-right',
      autoClose: 60000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
      draggablePercent: 40,
      className: 'toaster-css-2',
    })
  }

  getGameleaderboard() {
    this.props.dispatch(
      postRequest('games/game_leaderboard', {
        type: GAME_LEADERBOARD,
        values: { game: { code: this.props.match.params.game_code } },
      })
    )
  }

  loadPlayerGuessData(gameData) {
    if (gameData.game) {
      let channel_name = 'players_guess_data_' + gameData.game.id
      const channel = pusher.subscribe(channel_name)
      channel.bind('player_guess_event', data => {
        switch (data.type) {
          case 'update_remote_score':
            // this.getGameleaderboard()
            break
          case 'update_player_pick':
            this.noticeAlert('⚙️ PICK USED!' + `${data.data}` + ' is picking the next playlist!')
            break
          case 'player_muted':
            this.noticeAlert(':mute: PICK USED! ' + `${data.current_account}` + ' muted ' + `${data.muted_player}`)
            break
        }
      })
    }
  }

  getHelp() {
    HelpSection()
  }

  addRound() {
    Swal({
      title: 'WAIT!',
      text:
        "Did you do a drawing?  Drawings only work at the end of a round.  When you're ready, proceed to refresh player scores!",
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, Add New Round',
      cancelButtonText: 'Wait, I Still Need To Draw!',
    }).then(result => {
      if (result.value) {
        this.setState({ nextRound: true })
        this.props.dispatch(addNewRound({ game: { code: this.props.match.params.game_code } }))
        Swal({
          type: 'success',
          title: 'Added...',
          text: 'Round Added Successfully!',
          showConfirmButton: false,
          timer: 1500,
        })
      }
    })
  }

  showSlides = event => {
    this.setState({ slideOptions: true })
    this.props.dispatch(
      postRequest('games/pusher_update', {
        values: { game: { code: this.props.match.params.game_code, status: event } },
      })
    )
  }

  addFeedback = (song_id, cSong) => {
    if (cSong) this._updateCurrentSong = true
    Swal({
      title: 'Song Feedback',
      input: 'select',
      inputOptions: {
        short: 'short',
        loud: 'loud',
        quiet: 'quiet',
        explicit: 'explicit',
        'wrong artist': 'wrong artist',
        'wrong title': 'wrong title',
        'wrong playlist': 'wrong playlist',
        other: 'other',
      },
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Submit!',
    }).then(result => {
      if (result.value) {
        this.props.dispatch(
          postRequest('games/add_feedback', {
            type: ADD_FEEDBACK,
            values: { issue: result.value, song_id, code: this.props.match.params.game_code, role: 'host' },
          })
        )
      }
    })
  }

  _updateCurrentSong = false
  render() {
    const {
      nextRound,
      enableCurrentSongSkip,
      enableNextSongSkip,
      showBackgroundCheckbox,
      backgroundMusic,
      pusherCurrentSongCount,
      triviaMode,
    } = this.state
    const { current_song, leaderboard, songWinners, songsData, totalSongCount, songCount, skipping } = this.props.game
    return (
      <Container>
        {nextRound && <Timer time={0.1833333} data={'Next round will be added in'} position="no-position" />}
        {!nextRound && !triviaMode && (
          <div>
            <Row>
              <Col xs={12} md={6} mdOffset={3} id="Formstyle">
                <Row center="xs">
                  <Col xs={12} style={{ fontSize: '20px', padding: '5px', left: '10px', position: 'absolute' }}>
                    <b>CODE: </b>
                    {this.props.match.params.game_code}
                    <br />
                  </Col>
                  <Col xs>
                    <div>
                      <a
                        onClick={() => {
                          this.getHelp()
                        }}
                      >
                        <i className="fa fa-life-ring" /> Host Help
                      </a>
                    </div>
                  </Col>
                  <Col xs={12} style={{ fontSize: '20px', padding: '5px', right: '10px', position: 'absolute' }}>
                    {pusherCurrentSongCount ? pusherCurrentSongCount : this.props.game.currentSongCount}
                    <br />
                  </Col>
                </Row>
                <h4
                  style={{
                    color: '#fff',
                    fontWeight: 'bold',
                    marginBottom: '1rem',
                    marginTop: '3rem',
                    textAlign: 'center',
                  }}
                >
                  <a href={'/config/' + this.props.match.params.game_code + '?continue'}>Configure</a> | Control
                </h4>
                <Row center="xs">
                  <Col xs={12} style={{ marginTop: '1rem' }}>
                    <div style={{ textalign: 'center' }}>
                      {this._autoAdvance && (
                        <Button className="mayhem-btn-yellow" disabled>
                          ADVANCE
                        </Button>
                      )}
                      {!this._autoAdvance && songCount != null && (
                        <Button
                          className="mayhem-btn-yellow"
                          onClick={() => {
                            this.nextSongButton()
                          }}
                        >
                          ADVANCE
                        </Button>
                      )}
                    </div>
                  </Col>
                </Row>
                <br />
                <Row center="xs">
                  <div style={{ display: 'flex' }}>
                    <Col xs={6} sm={6} start="xs">
                      {' '}
                      <Button
                        className="mayhem-btn-yellow"
                        onClick={() => {
                          this.volumeButton('up')
                        }}
                      >
                        Vol Up
                      </Button>
                    </Col>
                    <Col xs={6} sm={6}>
                      {' '}
                      <Button
                        className="mayhem-btn-yellow"
                        onClick={() => {
                          this.volumeButton('down')
                        }}
                      >
                        Vol Down
                      </Button>
                    </Col>
                  </div>
                </Row>
                <Row center="xs">
                  <Col xs={12} style={{ marginTop: '1.5rem' }}>
                    {!this._openSession && (
                      <Button className="mayhem-btn-yellow" disabled>
                        Add Round
                      </Button>
                    )}
                    {this._openSession && songCount != totalSongCount && (
                      <Button className="mayhem-btn-yellow" disabled>
                        Add Round
                      </Button>
                    )}
                    {this._openSession && songCount != null && songCount == totalSongCount && (
                      <Button
                        onClick={() => {
                          this.addRound()
                        }}
                        className="mayhem-btn-yellow"
                      >
                        Add Round
                      </Button>
                    )}
                  </Col>
                </Row>
                {showBackgroundCheckbox && (
                  <div>
                    <div
                      className="remember-me"
                      onClick={() => {
                        this.setState({ backgroundMusic: !this.state.backgroundMusic })
                      }}
                    >
                      <div>{backgroundMusic && <i style={{ color: 'white' }} className="fa fa-check" />} </div>
                      <label>BACKGROUND MUSIC VOLUME</label>
                    </div>
                    {backgroundMusic && (
                      <Row center="xs">
                        <div style={{ display: 'flex' }}>
                          <Col xs={6} sm={6} start="xs">
                            {' '}
                            <Button
                              className="mayhem-btn-yellow"
                              onClick={() => {
                                this.volumeButton('bcg_up')
                              }}
                            >
                              {' '}
                              Vol +{' '}
                            </Button>
                          </Col>
                          <Col xs={6} sm={6}>
                            {' '}
                            <Button
                              className="mayhem-btn-yellow"
                              onClick={() => {
                                this.volumeButton('bcg_down')
                              }}
                            >
                              Vol -{' '}
                            </Button>
                          </Col>
                        </div>
                        <br />
                      </Row>
                    )}
                  </div>
                )}
                <br />
                {!skipping && (
                  <div>
                    {current_song && (
                      <Row>
                        <Col xs={10} xsOffset={1}>
                          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                            <h5>Current Song</h5>
                            {enableCurrentSongSkip && (
                              <b
                                style={{ color: '#ffcb4a', textDecoration: 'underline' }}
                                onClick={() => {
                                  this.skipNextSong()
                                }}
                              >
                                Skip
                              </b>
                            )}
                          </div>
                          <div className="song-info-css">
                            <b>Title: </b>
                            {current_song.title}
                            <br />
                            <b>Artist: </b>
                            {current_song.artist}
                            <br />
                            <b>Year: </b>
                            {current_song.year}
                            <br />
                            {true && (
                              <div>
                                <b style={{ color: 'red' }}>ISSUE: </b>
                                {current_song.issues}
                                {!current_song.issues && (
                                  <b
                                    style={{
                                      color: 'blue',
                                      textDecoration: 'underline',
                                      float: 'right',
                                      padding: '0rem 0.3rem',
                                    }}
                                    onClick={() => {
                                      this.addFeedback(current_song.id, true)
                                    }}
                                  >
                                    report issue
                                  </b>
                                )}
                              </div>
                            )}
                            <br />
                          </div>
                        </Col>
                      </Row>
                    )}
                    <br />
                    {current_song && this._showNextSongs && songsData && songsData.length != 0 && (
                      <Row>
                        <Col xs={10} xsOffset={1}>
                          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                            <h5>All Songs Info</h5>
                          </div>
                          {songsData.map((x, i) => (
                            <div key={i} className="song-info-css">
                              <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <b>Song {i + 1 + (totalSongCount - songsData.length) + '/' + totalSongCount} Info</b>
                                {enableNextSongSkip && (
                                  <b
                                    style={{ color: '#ffcb4a', textDecoration: 'underline' }}
                                    onClick={() => this.skipSelectedSong(x.id)}
                                  >
                                    Skip
                                  </b>
                                )}
                              </div>
                              <b>Title: </b>
                              {x.title}
                              <br />
                              <b>Artist: </b>
                              {x.artist}
                              <br />
                              <b>Year: </b>
                              {x.year}
                              <br />
                              <b style={{ color: 'red' }}>ISSUE: </b>
                              {x.issues}
                              {!x.issues && (
                                <b
                                  style={{
                                    color: 'blue',
                                    textDecoration: 'underline',
                                    float: 'right',
                                    padding: '0rem 0.3rem',
                                  }}
                                  onClick={() => {
                                    this.addFeedback(x.id, false)
                                  }}
                                >
                                  report issue
                                </b>
                              )}
                              <br />
                            </div>
                          ))}
                        </Col>
                      </Row>
                    )}
                  </div>
                )}
                {skipping && <div className="songSkipLoader">Loading...</div>}
              </Col>
            </Row>
            <Row center="xs">
              <div style={{ display: 'flex', paddingBottom: '2rem' }}>
                <Col xs={12} start="xs">
                  <a
                    style={{ color: 'white', background: 'red', padding: '1rem', paddingTop: '2rem' }}
                    onClick={() => {
                      this.gameOver()
                    }}
                  >
                    <i className="fa fa-trophy fa-3x" /> END GAME
                  </a>
                </Col>
              </div>
            </Row>
            <Row center="xs">
              <div
                className="col-xs-12 col-md-6"
                style={{ display: 'flex', padding: '.5rem', background: 'black', color: 'gray' }}
              >
                <Col xs>
                  <b>GAME RESCUE</b> - game stuck? try one of these
                </Col>
              </div>
            </Row>
            <Row center="xs">
              <div
                className="col-xs-12 col-md-6"
                style={{ display: 'flex', paddingBottom: '1rem', background: 'black', color: 'gray' }}
              >
                <Col xs>
                  <a
                    onClick={() => {
                      location.reload()
                    }}
                  >
                    <i className="fa fa-repeat fa-2x" />
                    <br /> 1) REFRESH
                  </a>
                </Col>
                <Col xs>
                  <a
                    onClick={() => {
                      this.reloadGame()
                    }}
                  >
                    <i className="fa fa-refresh fa-2x" />
                    <br /> 2) RECOVER
                  </a>
                </Col>
                <Col xs>
                  <a href={'/config/' + this.props.match.params.game_code + '?continue'} style={{ color: 'gray' }}>
                    <i className="fa fa-sign-in fa-2x" />
                    <br /> 3) RECONFIG
                  </a>
                </Col>
                <Col xs>
                  <a
                    onClick={() => {
                      this.resetGame()
                    }}
                  >
                    <i className="fa fa-power-off fa-2x" />
                    <br /> 4) RESET
                  </a>
                </Col>
              </div>
            </Row>
            <Row>
              <Col xs={12} md={6} mdOffset={3} style={{ padding: '0rem' }}>
                <div style={{ color: 'white' }}>refresh screen to update scores</div>
                {songWinners && <LeaderBoardTable songWinners itemsArray={songWinners} />}
              </Col>
            </Row>
            <br />
            <Row>
              <Col xs={12} md={6} mdOffset={3} style={{ padding: '0rem' }}>
                {leaderboard && <LeaderBoardTable itemsArray={leaderboard} />}
              </Col>
            </Row>
          </div>
        )}
        {!nextRound && triviaMode && (
          <div>
            <Col xs={12} md={6} mdOffset={3} id="SlideControl">
              <Row center="xs">
                <h5>Slide Controls</h5>
                <div style={{ display: 'flex', margin: '2rem' }}>
                  <Col xs={6} sm={6} start="xs">
                    {' '}
                    <Button
                      style={{ backgroundColor: 'transparent', borderColor: 'transparent' }}
                      className="fa fa-angle-double-left mayhem-btn-yellow"
                      onClick={() => {
                        this.showSlides('decrementSlide')
                      }}
                    />
                  </Col>
                  <Col xs={6} sm={6}>
                    {' '}
                    <Button
                      style={{ backgroundColor: 'transparent', borderColor: 'transparent' }}
                      className="fa fa-angle-double-right mayhem-btn-yellow"
                      onClick={() => {
                        this.showSlides('incrementSlide')
                      }}
                    />
                  </Col>
                </div>
                <br />
                <Row center="xs">
                  <Col xs={12}>
                    {' '}
                    <Button
                      className="mayhem-btn-yellow"
                      style={{ backgroundColor: 'transparent', borderColor: 'transparent', width: '300px' }}
                      onClick={() => {
                        this.showSlides('allowPlayerGuess')
                      }}
                    >
                      Let Player Guess
                    </Button>
                  </Col>
                </Row>
                <br />
              </Row>{' '}
            </Col>
            <Row>
              <Col xs={10} xsOffset={1}>
                <div style={{ paddingBottom: '1rem', display: 'flex', justifyContent: 'space-between' }}>
                  <a style={{ color: 'red' }} href={'/answers/' + this.props.match.params.game_code}>
                    <i className="fa fa-book" /> ANSWER
                  </a>
                  <a
                    style={{ color: 'red' }}
                    onClick={() => {
                      this.resetGame()
                    }}
                  >
                    <i className="fa fa-recycle" /> RESET
                  </a>
                  <a
                    style={{ color: 'red' }}
                    onClick={() => {
                      this.gameOver()
                    }}
                  >
                    <i className="fa fa-trophy" /> END
                  </a>
                </div>
              </Col>
            </Row>
          </div>
        )}
      </Container>
    )
  }
}

function mapStateToProps(store) {
  return {
    auth: store.auth,
    game: store.game,
  }
}

export default connect(mapStateToProps)(GameRemote)



