/* global location window*/
import React from 'react'
import { connect } from 'react-redux'
import { postRequest } from '../actions/gameAction'
import { checkUserIsLogin } from '../actions/loginActions'
import { GET_PLAYER_ANSWERS, GIFT_TICKETS } from '../constants/gameConstants'
import { Row, Col } from 'react-flexbox-grid'
import { Container } from 'reactstrap'
import { getGameData } from '../actions/hostGameActions'
import pusher from '../constants/pusher'
import Swal from 'sweetalert2'

class PlayerAnswer extends React.Component {
  UNSAFE_componentWillMount() {
    this.props.checkUserIsLogin().then(res => {
      if (!res) {
        Swal({
          position: 'center',
          type: 'warning',
          title: 'Please LogIn to Continue',
          showConfirmButton: false,
          timer: 2000,
        })
        this.props.history.push('/')
      } else {
        this.props.getGameData({ game: { code: this.props.match.params.game_code } })
        this.props.postRequest('games/player_answers', {
          type: GET_PLAYER_ANSWERS,
          values: { game: { code: this.props.match.params.game_code } },
        })
      }
    })
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this._runOnce && nextProps.game.game) {
      this._runOnce = false
      this.loadGameData(nextProps.game)
    }
  }

  loadGameData(gameData) {
    if (gameData.game) {
      let channel_name = 'games_' + gameData.game.id
      const channel = pusher.subscribe(channel_name)
      channel.bind('game_event', data => {
        switch (data.type) {
          case 'answers_updated':
            location.reload()
            break
          case 'start_new_game':
            var url = data.data.replace('/games/', '/answers/')
            window.location = url
            break
        }
      })
    }
  }

  dateFormat(date) {
    let time = new Date(date)
    let value = time.getHours() + ':' + time.getMinutes() + ':' + time.getSeconds()
    return value
  }

  sendTicketReward = val => {
    console.log(val, this.props)
    this.props.postRequest('player/gift_tickets', {
      type: GIFT_TICKETS,
      values: { ticket: { game_id: val.game_id, player_id: val.player_id } },
    })
  }

  _runOnce = true
  render() {
    const { player_answers } = this.props.game
    return (
      <div style={{ padding: '1rem', margin: '1vh 5vw', backgroundColor: 'white' }}>
        <a
          onClick={() => window.history.back()}
          style={{ color: '#ffca27', textDecoration: 'none', fontWeight: 'bold', lineHeight: '3.5' }}
        >
          <i className="fa fa-arrow-left" /> BACK
        </a>
        <h3 className="font-light" style={{ textAlign: 'center', padding: '1.5rem 0' }}>
          Player Answers
        </h3>
        <Container style={{ borderTop: '1px solid #ddd', borderBottom: '1px solid #ddd', padding: '1.5rem 0.5rem' }}>
          {player_answers &&
            player_answers.length != 0 &&
            Object.entries(player_answers).map((x, i) => (
              <div key={i}>
                {' '}
                <p style={{ color: '#210344', fontWeight: 'bold' }}>Round {i + 1} :</p>
                <Row>
                  <Col xs={1} style={{ textAlign: 'center' }}>
                    <p style={{ color: '#210344' }}>Question</p>
                  </Col>
                  <Col xs={1} style={{ textAlign: 'left' }}>
                    <p style={{ color: '#210344' }}>Sent_at</p>
                  </Col>
                  <Col xs={4} style={{ textAlign: 'left' }}>
                    <p style={{ color: '#210344' }}>Name</p>
                  </Col>
                  <Col xs={5} style={{ textAlign: 'left' }}>
                    <p style={{ color: '#210344' }}>Answer</p>
                  </Col>
                  <Col xs={1} style={{ textAlign: 'center' }}>
                    <p style={{ color: '#210344' }}>Ticket</p>
                  </Col>
                </Row>
                <div>
                  {' '}
                  {x[1].map((y, j) => (
                    <span key={j}>
                      <Row center="xs">
                        <Col xs={1} style={{ textAlign: 'center', color: 'green' }}>
                          <p>{y.question_number}</p>
                        </Col>
                        <Col xs={1} style={{ textAlign: 'left', color: 'green' }}>
                          <p>{this.dateFormat(y.created_at)}</p>
                        </Col>
                        <Col xs={4} style={{ textAlign: 'left', color: '#888' }}>
                          <p>{y.p_name}</p>
                        </Col>
                        <Col xs={5} style={{ textAlign: 'left', color: 'red' }}>
                          <p>{y.answer}</p>
                        </Col>
                        <Col xs={1} style={{ textAlign: 'center' }}>
                          <button onClick={() => this.sendTicketReward(y)} className="sendTicketButton">
                            AWARD
                          </button>
                        </Col>
                      </Row>{' '}
                    </span>
                  ))}{' '}
                </div>{' '}
              </div>
            ))}
        </Container>
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    checkUserIsLogin: () => dispatch(checkUserIsLogin()),
    postRequest: (path, params) => dispatch(postRequest(path, params)),
    getGameData: params => dispatch(getGameData(params)),
  }
}
export default connect(
  state => {
    return {
      game: state.game,
    }
  },
  mapDispatchToProps
)(PlayerAnswer)



